@font-face {
  font-family: rubik;
  src: url(./assets/fonts/Rubik/Rubik-VariableFont_wght.ttf);
}

@font-face {
  font-family: pacifico;
  src: url(./assets/fonts/Pacifico/Pacifico-Regular.ttf);
}

:root {
  --primary-color: #3C2317;
  --secondary-color: #628E90;
  --tertiary-color: #B4CDE6;
  --quaternary-color: #F5EFE6;
  --quinary-color: #040303;
}

* {
  box-sizing: border-box;
}

.blk-let {
  font-family: rubik;
  font-size: 50px;
  padding-top: 45px;
  color: var(--quinary-color);
  text-shadow: 0 0 8px var(--secondary-color);
}

.csv-let {
  font-family: pacifico;
  font-size: 80px;
  margin: 5px;
  color: var(--quinary-color);
  text-shadow: 0 0 8px var(--secondary-color);
}

@media screen and (max-width: 980) {

}

@media screen and (max-width: 768) {

}


@media screen and (max-width: 575px) {
  .blk-let {
    font-size: 30px;
    margin-top: 45px;
  }

  .csv-let {
    font-size: 50px;
    margin-top: 65px;
  }

  #home-text, #available {
    font-size: small;
  }

  #home-text {
    margin-bottom: 2px;
  }

  #carousel {
    margin-bottom: 2px;
    margin-top: 3px;
  }
  
  #home-btn {
    margin-top: 5px;
  }

  #background {
    min-height: 700px;
  }
}